.top-bar{
    position: relative;
    top:4%;
    left: 13%;
    width: 20%;
    height: 0%;
    
    background-color:#2a3950;
  }
  #upload-topbar{
    background-color: #355C7D;
    position: absolute;
    top: 20%;
    left: -43%;
    }
    #review-topbar{
    background-color: #725A7A;
    position: absolute;
    top: 20%;
    left: 120%;
    }
    #wallet-topbar{
    background-color: #C56C86;
    position: absolute;
    top: 20%;
    left: 282%;
    }
    .topbar-buttons{
      width: 25vw;
      height: 9vh;
      border-radius: 8px;
      box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.705);
      padding-bottom: 10%;
    
    }
    

    
    .topbar-buttons img{

      position: absolute;
      top: 50%;
      left: 50%;
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      width : 18vh;
      display:block;
      margin:auto;
    }
    
